import jwt from "jsonwebtoken";
import dotenv from "dotenv";
dotenv.config({ path: "../../.env" });

const tokenKey = process.env.REACT_APP_TOKEN_KEY;

export const jwtSign = (datas) => {
  const jwtSignData = jwt.sign(
    //  jwt  ,   defualt HS256
    { data: datas }, //   data
    tokenKey, //  secretKey,
    { expiresIn: "3d" } //  expire 
  );

  return jwtSignData;
};
export const jwtVerify = (datas) => {
  const jwtVerifyData = jwt.verify(datas, tokenKey).data; //  jwt   decode  data  
  return jwtVerifyData;
};
